  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/alumni-sans-semi-bold.woff2") format("woff2"), url("../fonts/alumni-sans-semi-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/alumni-sans-bold.woff2") format("woff2"), url("../fonts/alumni-sans-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/alumni-sans-extra-bold.woff2") format("woff2"), url("../fonts/alumni-sans-extra-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.title {
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 48px;
  line-height: 1.2;
  font-weight: 600;
  letter-spacing: 0.03em;
  color: #102654;
}
@media (max-width: 1199px) {
  .title {
    font-size: 42px;
    line-height: 1.2;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 32px;
    line-height: 1.2;
  }
}

.button {
  display: block;
  padding: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-size: 12px;
  line-height: 1.17;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
  border: 4px solid #ffffff;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    color: #14367a;
    background-color: #ffffff;
  }
}
.button.focus-visible:focus {
  color: #14367a;
  background-color: #ffffff;
}
.button.focus-visible:active {
  opacity: 0.6;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.button--disabled {
  opacity: 0.5;
  pointer-events: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  min-width: 320px;
  background-color: #14367a;
  background-image: conic-gradient(from 130deg at 16% 39%, #b80027 -47deg, #14367a 32deg, #b80027 313deg, #14367a 392deg);
}
@media (max-width: 1199px) {
  .header {
    background-image: conic-gradient(from 139deg at 21% 36%, #b80027 -47deg, #14367a 32deg, #b80027 313deg, #14367a 392deg);
  }
}
@media (max-width: 767px) {
  .header {
    background-image: conic-gradient(from 171deg at 35% 34%, #b80027 -47deg, #14367a 32deg, #b80027 313deg, #14367a 392deg);
  }
}

.header__wrapper {
  padding: 30px 60px 28px;
}
@media (max-width: 1199px) {
  .header__wrapper {
    padding: 35px 45px 82px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    max-width: 550px;
    padding: 15px;
    padding-bottom: 78px;
  }
}

.header__section {
  display: flex;
  justify-content: space-between;
  align-items: start;
}
@media (max-width: 1199px) {
  .header__section {
    margin-bottom: 18px;
  }
}

.header__logo {
  flex: 0 0 auto;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__logo.focus-visible:focus {
  opacity: 0.8;
}
.header__logo.focus-visible:active {
  opacity: 0.6;
}
@media (max-width: 1199px) {
  .header__logo {
    padding: 6px 0;
  }
}
@media (max-width: 767px) {
  .header__logo {
    padding: 0;
  }
}

.header__logo img {
  width: 122px;
  height: 44px;
}
@media (max-width: 1199px) {
  .header__logo img {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header__logo img {
    width: 69px;
    height: 24px;
  }
}

.header__phone {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 26px;
  line-height: 1.2;
  font-weight: 600;
  letter-spacing: 0.03em;
  color: #ffffff;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .header__phone:hover:not(.focus-visible) {
    color: #14367a;
  }
}
.header__phone.focus-visible:focus {
  color: #14367a;
}
.header__phone.focus-visible:active {
  color: #030a1a;
}
@media (max-width: 1199px) {
  .header__phone {
    font-size: 24px;
    line-height: 1.2;
    font-weight: 700;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header__phone {
    font-size: 20px;
    line-height: 1.2;
    font-weight: 600;
  }
}

.intro {
  display: grid;
  grid-template-columns: 397px 1fr;
  column-gap: 63px;
}
@media (max-width: 1199px) {
  .intro {
    grid-template-columns: 1fr 1fr;
    column-gap: 29px;
  }
}
@media (max-width: 767px) {
  .intro {
    grid-template-columns: 1fr;
    column-gap: 0;
  }
}

.intro__description {
  display: grid;
  grid-template-areas: "i-city i-city " "i-list i-title" "i-link i-link ";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content min-content;
  justify-items: end;
  align-items: start;
  column-gap: 18px;
  row-gap: 14px;
  margin-top: 88px;
}
@media (max-width: 1199px) {
  .intro__description {
    grid-template-columns: 1fr min-content;
    column-gap: 7.5%;
    row-gap: 8px;
    margin-top: 50px;
  }
}
@media (max-width: 767px) {
  .intro__description {
    column-gap: 0;
    row-gap: 14px;
  }
}

.intro__title {
  grid-area: i-title;
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 92px;
  line-height: 1.2;
  font-weight: 600;
  text-align: right;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  -webkit-text-stroke-width: 4px;
}
@media (max-width: 1199px) {
  .intro__title {
    font-size: 72px;
    line-height: 1.2;
    text-transform: none;
  }
}
@media (max-width: 767px) {
  .intro__title {
    margin-left: -10px;
  }
}

.intro__city {
  grid-area: i-city;
  margin: 0;
  margin-right: 52px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 48px;
  line-height: 1.2;
  font-weight: 600;
  letter-spacing: 0.03em;
  text-transform: lowercase;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .intro__city {
    margin-right: 4px;
    font-size: 34px;
    line-height: 1.2;
  }
}
@media (max-width: 767px) {
  .intro__city {
    font-size: 28px;
    line-height: 1.2;
  }
}

.intro__list {
  grid-area: i-list;
  align-self: end;
  margin: 0;
  margin-bottom: 35px;
  padding: 0;
  font-size: 14px;
  line-height: 1.5;
  text-align: right;
  color: #ffffff;
  list-style: none;
}
@media (max-width: 1199px) {
  .intro__list {
    margin-bottom: 23px;
  }
}
@media (max-width: 767px) {
  .intro__list {
    align-self: start;
    margin-top: 78px;
    margin-bottom: 0;
  }
}

.intro__link {
  grid-area: i-link;
  width: 59%;
  margin-top: 35px;
  padding: 20px;
  padding-bottom: 19px;
}
@media (max-width: 1199px) {
  .intro__link {
    width: 57.5%;
    margin-top: 26px;
    margin-right: -1px;
    margin-left: -1px;
  }
}
@media (max-width: 767px) {
  .intro__link {
    justify-self: center;
    width: 100%;
    margin: 0;
    margin-top: 9px;
    padding-bottom: 21px;
  }
}

.intro__image {
  position: relative;
  bottom: 23px;
  width: 71.4%;
}
@media (max-width: 1199px) {
  .intro__image {
    width: 100%;
    bottom: 0;
  }
}
@media (max-width: 767px) {
  .intro__image {
    display: none;
  }
}

.intro__image-wrapper {
  position: relative;
  padding-top: 111.2%;
}
@media (max-width: 1199px) {
  .intro__image-wrapper {
    padding-top: 120.1%;
  }
}

.intro__image img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.about {
  min-width: 320px;
}

.about__wrapper {
  display: grid;
  grid-template-columns: 480px minmax(560px, 660px);
  column-gap: 20px;
  padding: 120px 60px 6px;
}
@media (max-width: 1199px) {
  .about__wrapper {
    grid-template-columns: 1fr;
    column-gap: 0;
    padding: 101px 45px 52px;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    padding: 64px 0 52px;
  }
}

.about__section {
  padding: 39px 60px 0;
}
@media (max-width: 1199px) {
  .about__section {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .about__section {
    padding: 0 15px;
  }
}

.about__title {
  margin-bottom: 8px;
}
@media (max-width: 1199px) {
  .about__title {
    margin-bottom: 0;
    margin-left: -16px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .about__title {
    margin-bottom: 5px;
    margin-left: 0;
    text-align: start;
  }
}

.about__subtitle {
  margin: 0;
  margin-bottom: 40px;
  font-size: 18px;
  line-height: 1.5;
  color: #102654;
}
@media (max-width: 1199px) {
  .about__subtitle {
    margin-bottom: 34px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .about__subtitle {
    margin-bottom: 23px;
    text-align: start;
  }
}

.about__description {
  font-size: 16px;
  line-height: 1.5;
  font-weight: 300;
  color: #102654;
}
@media (max-width: 1199px) {
  .about__description {
    max-width: 85%;
    margin: 0 auto;
    margin-bottom: 42px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .about__description {
    max-width: 90%;
    margin: 0;
    margin-bottom: 33px;
    text-align: start;
  }
}

.about__text {
  margin: 0;
}

.about__text:not(:last-child) {
  margin-bottom: 8px;
}
@media (max-width: 1199px) {
  .about__text:not(:last-child) {
    margin-bottom: 0;
  }
}

@media (max-width: 1199px) {
  .about__text--additional {
    display: none;
  }
}

.about__image {
  position: relative;
  width: 100%;
}
.about__image::before {
  display: block;
  content: "";
  padding-top: 60.8%;
}
@media (max-width: 1199px) {
  .about__image::before {
    padding-top: 51%;
  }
}
@media (max-width: 767px) {
  .about__image {
    margin-bottom: 40px;
  }
  .about__image::before {
    padding-top: 98%;
  }
}

.about__image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.about__video {
  position: relative;
  top: -113px;
  left: -63px;
  width: 55.2%;
}
@media (max-width: 1199px) {
  .about__video {
    top: -85px;
    left: 50%;
    width: 39.3%;
    transform: translateX(-50%);
  }
}
@media (max-width: 767px) {
  .about__video {
    top: 0;
    left: 0;
    width: 100%;
    transform: none;
  }
}

.video__wrapper {
  position: relative;
  width: 100%;
  padding-top: 62.8%;
}
@media (max-width: 767px) {
  .video__wrapper {
    padding-top: 52%;
  }
}

.video__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.video__link::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  background: rgba(20, 54, 122, 0.5);
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.video__button {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100px;
  height: 100px;
  margin: auto;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("../img/decor/video-button.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: 0;
  transition: transform 0.3s ease;
  cursor: pointer;
}
.js-webp-exception .video__button {
  background-image: url("../img/decor/video-button.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .video__button {
    background-image: url("../img/decor/video-button@2x.webp");
  }
  .js-webp-exception .video__button {
    background-image: url("../img/decor/video-button@2x.jpg");
  }
}
@media (max-width: 1199px) {
  .video__button {
    width: 73px;
    height: 73px;
  }
}
@media (hover: hover) {
  .video__button:hover:not(.focus-visible) {
    transform: scale(1.1);
  }
}
.video__button.focus-visible:focus {
  transform: scale(1.1);
}
.video__button.focus-visible:active {
  opacity: 0.8;
}
.video__button:disabled {
  opacity: 0.5;
  pointer-events: none;
}

/* Классы для javaScript */
.is-available {
  cursor: pointer;
}

.is-available .video__button {
  display: block;
}

